<template>
        <ul @click="isOpenFunction" class="list-unstyled">
            <li v-if="object.img" class="d-flex link-body">
                <img class="icon" :src="loadImgFunction(object.img)">
                <span class="link-text">{{ object.title }}</span>
            </li>
        </ul>

        <transition name="fade">
            <!--Se verdadeiro torne bloco e não, invisivel -->
            <div :class="{ 'd-block': isOpen, 'd-none': !isOpen }" >
                <slot></slot>
            </div>
        </transition>
</template>

<script>
//Biblioteca para eventos
import mitt from 'mitt';

const emitter = mitt();

export default {
    name: "AccordionSubComponent",
    props: {
        object : Object
    },
    data() {
        return {
            isOpen: false,
        }
    },
    methods: {
        //transforme em verdadeiro e passe a instânicia com evento
        isOpenFunction() {
            this.isOpen = !this.isOpen;
            emitter.emit("eventIsOpen", this);
        },

        closeAccordionFunction() {
            this.isOpen = false;
        },

        loadImgFunction(value) {
            try {
                return require(`@/assets/img/icon/${value}`);
            } catch (error) {
                // Em caso de erro, repasse a informação e continua
                console.error(`Erro ao carregar imagem: ${error.message}`);
            }
        }
    },
    //quando criar o obejeto esculte o evento
    created() {
        // Registrar um ouvinte para o evento personalizado
        emitter.on("eventIsOpen", (sourceAccordion) => {
            if (sourceAccordion !== this) {
                this.closeAccordionFunction();
            }
        });
    },
}
</script>

