<script>
export default {
    name: 'AsideComponent',
    data() {
        return {
            iconStates: {
                button1: true,
                button2: true
            }
        };
    },
    computed: {
        iconClass() {
            // Esta computed property retorna uma função que aceita um argumento buttonKey
            return buttonKey => {
                // Determina as classes do ícone com base no estado do ícone correspondente a esse botão
                return this.iconStates[buttonKey] ? 'icon-caret bi-caret-down-fill' : 'icon-caret bi-caret-up-fill';
                // Substitua as classes acima pelos nomes das classes reais do seu ícone
            };
        }
    },
    methods: {
        togglerIcon(buttonKey) {
            // Inverte o estado do ícone específico para o botão fornecido
            this.iconStates[buttonKey] = !this.iconStates[buttonKey];
        }
    },
    props: {
        asideHide: String,
    }
};
</script>

