<template>
<div v-if="openClose" class="modal fade show">
  <div class="modal-dialog" >
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">Resultado da Pesquisa</h5>
      </div>
      <div class="modal-body">
        <slot class="modal-text"></slot>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-danger" @click="openCloseFunction">&times;</button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
 export default {
    name: 'ModalComponent',
    props:{
        visible: Boolean,
        modalTitle: String,
    },
    data(){
        return{
            openClose: this.visible,
        }
    },
    methods:{
        openCloseFunction(){
            this.openClose = !this.openClose;
            this.$emit('eventClose');
        }
    },
    watch:{
        visible: function(newVal){
            this.openClose = newVal;
        }
    }
 };
</script>

<style>
.modal{
  display: block !important;
}

.modal-title,.modal-text{
    color: #8891A1 !important;
    font-size: 14px !important;
    font-family: 'Poppins-Regular' !important;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 4s;
  /* Ajuste a duração conforme necessário */
}
</style>