<template>
  <ul v-if="link.sublinks" class="list-unstyled">
    <li v-for="sublink in link.sublinks" :key="sublink.id" class="d-flex link-body">
      <i class="icon" :class="sublink.icon"><img class="icon" v-if="sublink.img" :src="loadImgFunction(sublink.img)"></i>
      <a class="link-text" :href="sublink.url" target="_blank">{{ sublink.text }}</a>
    </li>
  </ul>
</template>
  
<script>

export default {
  name: "LinkComponent",
  props: {
    link: Object,
  },
  methods:{
    loadImgFunction(value){
      try {
        return require(`@/assets/img/icon/${value}`);
      } catch (error) {
        // Em caso de erro, repasse a informação e continua
        console.error(`Erro ao carregar imagem: ${error.message}`);
      }
    }
  }
};
</script>

<style>
@import url("../assets/css/icon.css");

.link-body {
  position: relative;
  padding: 8px 0px 8px 12px;
  transition: transform 0.5s ease;
  margin-top: 0px;
  margin-bottom: 14px;
}

.link-body:hover{
  border: none;
  background-color: rgba(255, 255, 255, 0.9);
  transform: translateY(-2px);
}

.link-body::before{
  content: '';
    background: #447EF7;
    position: absolute;
    left: 0.2rem;
    top: 0;
    width: 0.2rem;
    height: 100%;
    transform: translateX(-0.4rem);
    transition: 0.3s;
    opacity: 0;
}

.link-body:hover::before{
  opacity: 1;
  transform: translateX(0);
}

.link-text{
  color: #333333;
  font-family: 'Poppins-Regular';
  font-size: 13px;
}
</style>
  