<template>
    <header class="fixed-top">
        <nav class="navbar navbar-expand-lg navbar-light" :class="currentClass">
            <!--Objeto Toggler repassando o click no botao para a função alterTagFunction-->
            <TogglerComponent @eventClickToggler="alterTagFunction"></TogglerComponent>

            <!--Formulário de Pesquisa-->
            <form class="form-inline">
                <div class="input-group">
                    <div class="input-group-prepend">
                        <span class="input-group-text icon-search"><i class="bi bi-search"></i></span>
                    </div>
                    <input type="search" class="form-control input-search" placeholder="Pesquisar...">
                </div>
            </form>
            <!--Formulario de Pesquisa Fim-->
        </nav>
    </header>
</template>
  
<script>
import TogglerComponent from './TogglerComponent.vue';

export default {
    name: 'NavHeader',
    components: {
        TogglerComponent,
    },
    data() {
        return {
            currentClass: 'offset-2',
        };
    },
    methods: {
        alterTagFunction() {
            this.currentClass = this.currentClass === 'offset-0' ? 'offset-2' : 'offset-0';
            this.$emit('eventCliclkToggler');
        },
    },
};
</script>
  
<style scope>
.navbar {
    transition: all 0.3s ease;
}
</style>